body {
  overflow: auto; // Стандартний стан прокрутки

  &.no-scroll {
      overflow: hidden; // Вимикаємо прокрутку при наявності класу no-scroll
  }
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #e5e6e8;
  font-weight: 700;
  text-transform: uppercase;
  overflow-y: auto; // Прокрутка всього меню
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  
  
  &__phone-number {
      color: inherit;
      text-decoration: none;
      font-size: 16px;
      line-height: 22px;
      display: block;
      margin-bottom: 16px;
  }

  &__call-to-order {
      display: block;
      color: inherit;
      text-decoration: none;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 2px;
      padding-bottom: 7px;
      border-bottom: 1px solid #000;
      width: fit-content;
  }

  &__nav {
      margin-bottom: 48px;
  }

  &__top {
      padding-bottom: 32px;
  }

  .container {
      flex: 1; // Дозволяє контейнеру займати всю доступну висоту
      overflow-y: auto; // Додаємо вертикальну прокрутку для контейнера всередині меню
  }
}
